import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { MessagesPage } from '../messages';

@Component({
    selector: 'messageList',
    templateUrl: 'messageList.html'
})
export class MessageListPage {
    messages: { title: string; categoryColor: string; categoryName: string; createdAt: string; closedOn: string; isClosed: string; }[];

    constructor(public navCtrl: NavController, public navParams: NavParams) {
        this.messages = [
            {
                title: 'Annual Day',
                categoryColor: '#673AB7',
                categoryName: 'Extra',
                createdAt: '12/9/2017',
                closedOn: '15/10/2017',
                isClosed: '1',
            },
            {
                title: 'Results',
                categoryColor: '#4CAF50',
                categoryName: 'Exams',
                createdAt: '12/9/2017',
                closedOn: '19/10/2017',
                isClosed: '1',
            },
            {
                title: 'College Holiday',
                categoryColor: '#00BCD4',
                categoryName: 'College',
                createdAt: '17/7/2017',
                closedOn: null,
                isClosed: null,
            },
            {
                title: 'Exam date',
                categoryColor: '#4CAF50',
                categoryName: 'Exams',
                createdAt: '17/9/2017',
                closedOn: '19/9/2017',
                isClosed: '1',
            },
        ]
    }

    openMessageView(message){
        this.navCtrl.push(MessagesPage, {'message': message});
    }
    
}



