import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { LoginPage } from '../pages/login/login';
import { SharedService } from '../providers/shared.service';
import { ProfilePage } from '../pages/profile/profile';
import { DueAssignmentsPage } from '../pages/assignments/dueAssignments/dueAssignments';
import { AssessmentPage } from '../pages/assessment/assessment';
import { EventsPage } from '../pages/events/events';
import { CircularPage } from '../pages/circular/circular';
import { CircularListPage } from '../pages/circular/circularList/circularList';
import { MessageListPage } from '../pages/messages/messageList/messageList';
import { DateSheetPage } from '../pages/dateSheet/dateSheet';
import { DateSheetListPage } from '../pages/dateSheet/sheetList/sheetList';
import { ElectiveListPage } from '../pages/elective/sheetList/electiveList';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  category: string;
  faculty: Array<{ title: string, component: any, icon:any }>;
  students: Array<{ title: string, component: any, icon:any }>;
  hod: Array<{ title: string, component: any, icon:any }>;
  principal: Array<{ title: string, component: any, icon:any }>;
  admin: Array<{ title: string, component: any, icon:any }>;
  @ViewChild(Nav) nav: Nav;

  rootPage: any = LoginPage;
  username: string;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, 
              public statusBar: StatusBar, 
              public splashScreen: SplashScreen,
              public event: Events,
              public sharedService:SharedService) {
    
    this.initializeApp();

    // used for an example of ngFor and navigation

    this.students = [
      { title: 'Dashboard', component: HomePage, icon: 'http://www.eq2llc.com/wp-content/uploads/icon-dashboard.png' },
      { title: 'Profile', component: ProfilePage, icon: 'https://www.shareicon.net/download/2016/09/01/822739_user_512x512.png' },
      { title: 'Due Assignments', component: DueAssignmentsPage, icon: 'http://www.iconshock.com/img_jpg/SIGMA/project_managment/jpg/256/responsibility_assignment_icon.jpg' },
      { title: 'Assessments', component: AssessmentPage, icon: 'https://cdn3.iconfinder.com/data/icons/brain-games/128/Quiz-Games.png' },
      { title: 'Date Sheet ', component: DateSheetListPage, icon: 'https://lh4.ggpht.com/TUyR16LfVjlNA-K9-coUNtUYqVmu8mpi_9_ZQEXlJAO709FgsDX8b7jZwhW4BSwpCQ=w300' },
      { title: 'Events', component: EventsPage, icon: 'http://icons.iconarchive.com/icons/wwalczyszyn/android-style/256/Calendar-icon.png' },
      { title: 'Circular', component: CircularListPage, icon: 'http://www.geetabalbharti.in/images/footer/circulars.png' },
      { title: 'Messages', component: MessageListPage, icon: 'http://www.iconsdb.com/icons/preview/orange/message-xxl.png' },
      { title: 'Electives', component: ElectiveListPage, icon: 'https://maxcdn.icons8.com/Share/icon/ultraviolet/Science//elective1600.png' },
      { title: 'Logout', component: LoginPage, icon: 'http://icons.iconarchive.com/icons/alecive/flatwoken/512/Apps-Dialog-Logout-icon.png' },
    ]

    this.faculty = [
      { title: 'Dashboard', component: HomePage, icon: 'xyz.png' },
      { title: 'Faculty List', component: ListPage, icon: 'xyz.png' }
    ]

    this.hod = [
      { title: 'Dashboard', component: HomePage, icon: 'xyz.png' },
      { title: 'HOD List', component: ListPage, icon: 'xyz.png' }
    ]

    this.principal = [
      { title: 'Dashboard', component: HomePage, icon: 'xyz.png' },
      { title: 'principal List', component: ListPage, icon: 'xyz.png' }
    ]

    this.admin = [
      { title: 'Dashboard', component: HomePage, icon: 'xyz.png' },
      { title: 'Admin List', component: ListPage, icon: 'xyz.png' }
    ]


  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.event.subscribe("user:login", (data) => {
        if(data.category == 'student')
          this.pages = this.students;
        else if(data.category == 'faculty')
          this.pages = this.faculty;
        else if(data.category == 'hod')
          this.pages = this.hod;
        else if(data.category == 'principal')
          this.pages = this.principal;
        else if(data.category == 'admin')
          this.pages = this.hod;
        
          console.log(data.name);
          this.username=data.name;
      })
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  openAccountPage(){
    this.nav.push(ProfilePage);
  }
}



